.cell .fill-from-link {
	margin-right: 8px;
}

.vatid-message {
	position: relative;
	display: flex;
	padding: 0.5em 1em;
	border-radius: 1em;
	align-items: center;
	justify-content: center;
}

.vatid-message :first-child {
	flex: 1 1;
}

.vatid-message :not(:first-child) {
	flex: 0 0;
}

.vatid-message.valid {
	background: #00a500;
	color: #ffffff;
}

.vatid-message.invalid {
	background: #b6b600;
	color: #000000;
}

.vatid-tooltip-container .vatid-tooltip-title {
	font-weight: bold;
	font-size: 18px;
	text-align: center;
	display: block;
}

.vatid-tooltip-container hr {
	margin: 0.5em 0;
	border: 0;
	border-top: 1px solid #dadada;
}

.email-pdf-icon {
	padding-right: 7px;
}

.print-pdf-icon {
	padding-right: 10px;
}

