.vat-id-box {
	position: relative;
}

.vat-id-info-box {
	color: #000;
    z-index: 10;
    position: absolute;
    white-space: break-spaces;
    text-align: justify;
    top: 117%;
	width: 400px;
    right: 0;
    padding: 13px 15px;
    background: #fff;
    box-shadow: 0 0 10px #0000004f;
    border-radius: 5px;
	display: none;
}

.vat-id-box .fa-info-circle:hover ~ .vat-id-info-box {
    display: block;
}